.about__section {
  background-image: url('../images/about-bg.jpeg');
  min-height: 620px;
  background-size: contain;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-position: center;
}

.about__container {
  padding-top: 50px;
  padding-bottom: 50px;
  display: flex;
  flex-direction: column;
}

@keyframes lefttoright {
  0% {
    transform: translateX(-130%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes righttoleft {
  0% {
    transform: translateX(130%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.about__wrap {
  max-width: 70%;
  border: 2px solid var(--text-color);
  padding: 40px 30px;
  margin-bottom: 30px;
  display: flex;
  align-items: center;
  opacity: 0;
  transition: scale 250ms cubic-bezier(0.4, 0, 0.2, 1);

  // &:nth-child(2n + 1) {
  //   animation: lefttoright 600ms linear 500ms 1 forwards;
  // }
  // &:nth-child(2n) {
  //   align-self: end;
  //   animation: righttoleft 600ms linear 500ms forwards;
  // }
  &:hover {
    // cursor: pointer;
    scale: 105%;
  }
}

.about__wrap--left.action {
  animation: lefttoright 600ms linear forwards;
}
.about__wrap--right.action {
  align-self: end;
  animation: righttoleft 600ms linear forwards;
}

.about__icon {
  width: 150px;
  margin-right: 30px;
}
