.contact__section {
}

.contact__container {
}
.contact__wrap {
  background-color: var(--text-color);
  padding: 20px 30px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.contact__text-wrap {
  margin-right: 150px;
}
.contact__heading {
  color: var(--bg-acent-color);
  margin-bottom: 5px;
}
.contact__desc {
  color: var(--bg-acent-color);
}
.secondary__button {
  min-width: 230px;
  padding: 18px 25px;
  border: 2px solid var(--bg-color);
  font-weight: 700;
  transition: scale 250ms cubic-bezier(0.4, 0, 0.2, 1),
    border-width 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    cursor: pointer;
    scale: 1.04;
    border-width: 2px;
    color: var(--text-active-color);
    background-color: var(--bg-acent-color);
  }
}
